'use client'

import { useState, useEffect } from 'react'
import axios from 'axios'
import { Search, Loader2, ArrowRight, AppWindow } from 'lucide-react'
import { motion, AnimatePresence } from 'framer-motion'
import Link from 'next/link'
import { useRouter } from 'next/navigation'
import { getTimeBasedTheme } from '@/utils/theme'

export default function AppSelectionPage() {
    const router = useRouter()
    const [searchTerm, setSearchTerm] = useState('')
    const [searchResults, setSearchResults] = useState([])
    const [isSearching, setIsSearching] = useState(false)
    const [error, setError] = useState(null)

    // Theme Management
    useEffect(() => {
        const updateTheme = () => {
            const theme = getTimeBasedTheme();
            if (theme === 'light') {
                document.body.classList.add('light-mode');
            } else {
                document.body.classList.remove('light-mode');
            }
        };

        updateTheme();
        const interval = setInterval(updateTheme, 60000);
        return () => clearInterval(interval);
    }, []);

    const processSearch = async (e) => {
        e.preventDefault()
        const term = searchTerm.trim()
        if (!term) return

        setError(null)
        setSearchResults([])

        // 1. Direct Link/ID Navigation strategy (Zero API)
        // Check if URL
        if (term.includes('play.google.com')) {
            try {
                const url = new URL(term);
                const id = url.searchParams.get('id');
                if (id) {
                    router.push(`/tracker?appId=${id}`);
                    return;
                }
            } catch (e) { }
        }

        // Check if likely Package ID (contains dot, no spaces)
        if (term.includes('.') && !term.includes(' ')) {
            router.push(`/tracker?appId=${term}`);
            return;
        }

        // 2. Search API Strategy (Only if needed)
        setIsSearching(true)
        try {
            const { data } = await axios.get(`/api/apps/search?term=${term}`)
            if (data.length === 0) {
                setError('No apps found')
            } else {
                setSearchResults(data)
            }
        } catch (error) {
            setError('Search failed. Please try again.')
        } finally {
            setIsSearching(false)
        }
    }

    return (
        <div className="max-w-4xl mx-auto px-6 py-20 min-h-[80vh] flex flex-col justify-center items-center space-y-12">

            {/* Minimalist Header */}
            <header className="space-y-4 text-center">
                <h1 className="text-5xl font-bold tracking-tight text-[var(--text-main)]">
                    App Tracker
                </h1>
                <p className="text-[var(--text-muted)] text-lg max-w-md mx-auto">
                    Enter a Google Play URL or App ID to begin analysis immediately.
                </p>
            </header>

            {/* Core Interaction Area */}
            <div className="w-full max-w-xl space-y-6">
                <form onSubmit={processSearch} className="relative group">
                    <Search className="absolute left-5 top-1/2 -translate-y-1/2 text-slate-400 w-5 h-5" />
                    <input
                        type="text"
                        placeholder="e.g. com.instagram.android or Search..."
                        className="pro-input pl-14 pr-24 py-4 text-lg" // Added padding-right for button
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                    />
                    <div className="absolute right-2 top-1/2 -translate-y-1/2">
                        <button
                            type="submit"
                            disabled={isSearching || !searchTerm.trim()}
                            className="pro-button py-2 px-4 text-sm"
                        >
                            {isSearching ? <Loader2 className="w-4 h-4 animate-spin" /> : 'Analyze'}
                        </button>
                    </div>
                </form>

                {/* Error State */}
                {error && (
                    <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="text-center text-red-500 text-sm font-medium">
                        {error}
                    </motion.div>
                )}
            </div>

            {/* Results Display (Only if keyword search was used) */}
            <AnimatePresence mode="wait">
                {searchResults.length > 0 && (
                    <motion.div
                        key="results"
                        initial={{ opacity: 0, y: 10 }}
                        animate={{ opacity: 1, y: 0 }}
                        className="w-full max-w-xl space-y-3"
                    >
                        <div className="text-xs font-semibold uppercase tracking-wider text-[var(--text-muted)] pl-2">Tap to Select</div>
                        {searchResults.map((app) => (
                            <Link
                                key={app.appId}
                                href={`/tracker?appId=${app.appId}&title=${encodeURIComponent(app.title)}&icon=${encodeURIComponent(app.icon)}&developer=${encodeURIComponent(app.developer)}`}
                            >
                                <div className="glass-card p-4 flex items-center gap-4 group cursor-pointer hover:border-blue-500/50">
                                    <img src={app.icon} className="w-12 h-12 rounded-xl" alt="" />
                                    <div className="flex-1 min-w-0">
                                        <h3 className="font-medium text-[var(--text-main)] truncate">{app.title}</h3>
                                        <p className="text-sm text-[var(--text-muted)] truncate">{app.developer}</p>
                                    </div>
                                    <ArrowRight className="w-4 h-4 text-[var(--text-muted)] group-hover:text-blue-500 transition-colors" />
                                </div>
                            </Link>
                        ))}
                    </motion.div>
                )}
            </AnimatePresence>
        </div>
    )
}
