export const getTimeBasedTheme = () => {
    try {
        // Just use the system local time
        const hour = new Date().getHours();

        // Light mode between 6 AM (6) and 6 PM (18)
        return (hour >= 6 && hour < 18) ? 'light' : 'dark';
    } catch (e) {
        return 'dark'; // Fallback
    }
};
